import {ListItemID} from '../../../layouts/ListLayout'
import {MasonryLayout} from '../../../layouts/MasonryLayout'
import {notExisting} from '../../../typecheck/existing'
import {assume} from '../../../utils/assume'
import {ensureFocusOffset} from './ensureFocusOffset'

export function calcFocusScroll(focus: Maybe<ListItemID>,
                                focusOffset: Maybe<px>,
                                layout: MasonryLayout,
                                height: px): px {
    const focusItem = assume(focus, focus => layout.getByID(focus))
    if (notExisting(focusItem))
        return 0
    const offset = ensureFocusOffset(focusOffset, height)
    const scroll = focusItem.offset - offset + (offset / height) * focusItem.height
    return scroll
}
