import * as React from 'react';
import * as ReactDOM from 'react-dom'
import {ListItemID} from '../../../layouts/ListLayout'
import {nothing} from '../../../utils/nothing'
import {InfiniteList} from '../InfiniteList'
import {ItemProps} from '../src/ItemProps'
import {SegmentExample} from './SegmentExample'

const itemIDs: Array<ListItemID> = []
for (let i = 0; i < 10000; i++) {
    itemIDs.push(i.toString())
}

export class Demo extends React.Component<{}, {
    width: number,
    height: number,
    focusOffset: number,
    focus: Maybe<ListItemID>,
    columns: number
}> {
    render() {
        const {focusOffset, focus, columns} = this.state
        const {Item, onScroll} = this

        return <div style={{position: 'relative', width: '100%', height: '100%'}}>
            <InfiniteList {...{itemIDs, focusOffset, focus, Item, columns}}
                          onScroll={onScroll}/>
            <div style={{position: 'fixed', right: 20, bottom: 20}}
                 onClick={this.scrollToTop} onTouchEnd={this.scrollToTop}>
                <svg style={{height: 52, width: 52}} viewBox="0 0 24 24">
                    <path fill="#FFFFFF" d="M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z"/>
                    <path fill="#000000" d="M12,22A10,10 0 0,1 2,12A10,10 0 0,1 12,2A10,10 0 0,1 22,12A10,10 0 0,1 12,22M12,7L7,12H10V16H14V12H17L12,7Z"/>
                </svg>
            </div>
        </div>
    }

    componentDidMount() {
        window.addEventListener('resize', () => {
            const width = window.innerWidth
            const height = window.innerHeight
            const columns = Math.floor(window.innerWidth / 300)
            this.setState({width, height, columns})
        })
    }

    constructor(props: {}) {
        super(props)
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight,
            focusOffset: 0,
            focus: nothing,
            columns: Math.floor(window.innerWidth / 300)
        }
    }

    private onScroll = (scroll: px) => {
        if (Math.abs(scroll) < 10) {
            this.setState({focusOffset: 0})
        } else {
            this.setState({focusOffset: this.state.height / 2})
        }
    }

    private scrollToTop = () => {
        this.setState({focusOffset: 0})
        this.focusOn(itemIDs[0])
    }

    private Item = ((parent: this) => class extends React.PureComponent<ItemProps> {
        render() {
            const updateFocus = () => {
                parent.focusOn(this.props.id)
            }
            return <div onClick={updateFocus} style={{width: '100%'}}>
                <SegmentExample id={this.props.id}/>
            </div>
        }
    })(this)

    private focusOn = (id: ListItemID) => {
        this.setState({focus: nothing}, () =>
            this.setState({focusOffset: this.state.height / 2, focus: id})
        )
    }
}

const root = document.getElementById('root')!
ReactDOM.render(
    //
    // <ErrorBoundary FallbackComponent={({componentStack, error}) =>
    //     <pre style={{width: window.innerWidth, wordBreak: 'break-all'}}>
    //         <p>{componentStack}</p>
    //         <p>{error.name}</p>
    //         <p>{error.stack}</p>
    //         <p>{error.message}</p>
    //     </pre>
    // }>
    //
        <Demo/>
    // </ErrorBoundary>
    , root
)

