export const isInteger = Number['isInteger']
    || ((v: number) => typeof v === 'number' && isFinite(v) && Math.floor(v) === v)
// https://github.com/Microsoft/fast-binary-indexed-tree-js/blob/master/lib/index.js

export const isPositiveInteger = (n: number) => isInteger(n) && n > 0

export function isNonpositiveInteger(n: number): boolean {
    return isPositiveInteger(n) || n === 0
}
