import {byIndex} from '../../../layouts/byIndex'
import {MasonryItemPosition, MasonryLayout} from '../../../layouts/MasonryLayout'
import {assume} from '../../../utils/assume'
import {ensure} from '../../../utils/ensure'

const pageSize = 10

export function calcSegments(from: px, to: px,
                             layout: MasonryLayout): Array<MasonryItemPosition> {
    const floorIndex = assume(layout.getByOffset(from).sort(byIndex)[0], it => it.index)
    const floor = Math.floor(ensure(floorIndex, 0) / pageSize) * pageSize

    const ceilIndex = assume(layout.getByOffset(to).sort(byIndex).pop(), it => it.index)
    const ceil = Math.ceil(ensure(ceilIndex, layout.size - 1) / pageSize) * pageSize

    const now = layout.getByIndexRange(floor, ceil)

    return now
}


