import {applyMiddleware} from './applyMiddleware'
import {createPublishers} from './createPublishers'
import {createSubjects} from './createSubjects'
import {Observables} from './Observables'
import {Pipes} from './Pipes'

export function createDispatcher<T extends object>(
    middleware: Pipes<Required<T>>
): Dispatcher<Required<T>> {
    const subjects = createSubjects<Required<T>>(middleware)
    return Object.assign(
        {publish: createPublishers<Required<T>>(subjects)},
        applyMiddleware<Required<T>>(subjects, middleware)
    )
}

export type Dispatcher<T extends object> =
    { publish: EventHandlers<Required<T>> } &
    Observables<Required<T>>

