import * as React from 'react'
import {CSSProperties} from 'react'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {interval} from 'rxjs/observable/interval'
import {Observer} from 'rxjs/Observer'
import {mapTo, skip, tap} from 'rxjs/operators'
import {bufferCount} from 'rxjs/operators/bufferCount'
import {bufferTime} from 'rxjs/operators/bufferTime'
import {combineLatest} from 'rxjs/operators/combineLatest'
import {distinctUntilChanged} from 'rxjs/operators/distinctUntilChanged'
import {filter} from 'rxjs/operators/filter'
import {map} from 'rxjs/operators/map'
import {merge} from 'rxjs/operators/merge'
import {pairwise} from 'rxjs/operators/pairwise'
import {sampleTime} from 'rxjs/operators/sampleTime'
import {scan} from 'rxjs/operators/scan'
import {shareReplay} from 'rxjs/operators/shareReplay'
import {startWith} from 'rxjs/operators/startWith'
import {switchMap} from 'rxjs/operators/switchMap'
import {takeUntil} from 'rxjs/operators/takeUntil'
import {timestamp} from 'rxjs/operators/timestamp'
import {withLatestFrom} from 'rxjs/operators/withLatestFrom'
import {animationFrame} from 'rxjs/scheduler/animationFrame'
import {Subscription} from 'rxjs/Subscription'
import {Memoize} from 'typescript-memoize'
import {ListItemID, ListItemPosition, ListItemShape} from '../../layouts/ListLayout';
import {MasonryItemPosition, MasonryLayout} from '../../layouts/MasonryLayout'
import {MasonryLayoutImpl} from '../../layouts/MasonryLayoutImpl'
import {measuring} from '../../react/measure/measuring'
import {createDispatcher} from '../../rxjs/createDispatcher'
import {log$} from '../../rxjs/log$'
import {Pass} from '../../rxjs/Pipe';
import {existing} from '../../typecheck/existing'
import {isFalse} from '../../typecheck/isFalse';
import {isTrue} from '../../typecheck/isTrue';
import {assume} from '../../utils/assume'
import {byIndex} from '../../utils/byIndex'
import {ensure} from '../../utils/ensure'
import {nothing} from '../../utils/nothing'
import {publishAll} from '../../utils/publishAll'
import {Pulse} from '../../utils/Pulse'
import {leastDeviation} from '../../utils/statics';
import {calcOutline} from '../../view/calcOutline'
import {terminateTween, tweenHere} from '../../view/tweenHere'
import {calcFocusScroll} from './src/calcFocusScroll'
import {calcSegments} from './src/calcSegments'
import {ensureFocusOffset} from './src/ensureFocusOffset'
import {ItemProps} from './src/ItemProps';
import deepEqual = require('deep-equal')

export class InfiniteList extends React.Component<Props, State, Snapshot> {
    public render(): JSX.Element {
        const {scrolled$, heightUpdated$, touchEnded$, touchMoved$, touchStarted$} = this.actions$.publish
        const {state, children, adjustScroll, Segment} = this
        const {segments, layout} = state

        const layoutHeight = layout.totalHeight

        const containerStyle: CSSProperties = {
            position: 'relative',
            display: 'block',
            width: '100%',
            height: '100%',
            overflowY: 'scroll',
            overflowX: 'hidden',
            // https://stackoverflow.com/questions/42205462/disable-chromes-scroll-preserving-feature
            overflowAnchor: 'none',
            scrollBehavior: 'auto',
            WebkitOverflowScrolling: 'touch',
            contain: 'strict',
            // https://bugs.chromium.org/p/chromium/issues/detail?id=514303
            willChange: 'transform'
        }

        const contentStyle: CSSProperties = {
            position: 'absolute',
            display: 'block',
            width: '100%',
            height: layoutHeight,
            contain: 'strict',
            willChange: 'transform, height'
        }

        const viewportStyle: CSSProperties = {
            position: 'absolute',
            display: 'flex',
            alignItems: 'stretch',
            width: '100%',
            height: layoutHeight,
            transform: adjustScroll === 0 ? 'none' : `translateY(${-adjustScroll}px)`,
            contain: 'strict',
            willChange: 'transform, height'
        }

        const columnCount = layout.columns

        const columnStyle = (column: number): CSSProperties => ({
            position: 'relative',
            height: '100%',
            flexGrow: 1,
            contain: 'size layout style',
            zIndex: column
        })

        const paddingStyle = (column: ReadonlyArray<MasonryItemPosition>): CSSProperties => ({
            height: existing(column[0]) ? column[0].offset : 0
        })

        const columns = new Array(columnCount)
            .fill(nothing)
            .map((_, column) => segments.filter(it => it.column === column))

        return <Root style={containerStyle}
                     innerRef={ref => assume(ref, ref => children.container = ref)}
                     onScroll={scrolled$}
                     onHeightChange={heightUpdated$}
                     onTouchStartCapture={touchStarted$}
                     onTouchEndCapture={touchEnded$}
                     onTouchMoveCapture={touchMoved$}>
            <div style={contentStyle}
                 ref={ref => assume(ref, ref => children.content = ref)}>
                <div style={viewportStyle}
                     ref={ref => assume(ref, ref => children.viewport = ref)}>
                    {columns.map((column, columnNum) =>
                        <div key={columnNum} style={columnStyle(columnNum)}>
                            <div key={'paddingTop'} style={paddingStyle(column)}/>
                            {column.map(spec =>
                                <Segment key={spec.id} {...{...state, ...spec}}/>
                            )}
                        </div>
                    )}
                </div>
            </div>
        </Root>
    }

    componentDidMount() {
        this.work.add(this.updateState())
        this.work.add(this.callEventHandlers())
        this.work.add(this.performContentJump())
        this.work.add(this.applyAdjustedScroll())
        publishAll<Props>(this.props, this.props$.publish)
        this.setInitialScroll()
    }

    getSnapshotBeforeUpdate(prevProps: Props, prevState: State): Snapshot {
        const {containerScroll, adjustScroll, height} = this
        const {segments} = this.children
        const {layout} = this.state

        let anchor: Maybe<Anchor> = nothing
        if (prevState.layout !== layout) {
            const focusOffset = ensureFocusOffset(prevProps.focusOffset, height)
            const outlines = new Map<ListItemID, Outline>()
            segments.forEach((element, id) => outlines.set(id, calcOutline(element)))
            const scroll = containerScroll + adjustScroll
            const focusItem = prevState.layout.getByOffset(scroll + focusOffset).sort(byIndex)[0]
            anchor = assume(focusItem, focusItem => {
                const aboveFocus = (focusOffset - (focusItem.offset - scroll)) / focusItem.height
                const id = focusItem.id
                return {id, aboveFocus, outlines}
            })
        }

        return {anchor}
    }

    componentDidUpdate(prevProps: Props, prevState: State, snapshot: Snapshot) {
        const {props} = this

        if (existing(snapshot.anchor)) {
            const {anchor} = snapshot
            const {segments} = this.children
            const {height, containerScroll} = this
            const {layout} = this.state

            const anchorItem = layout.getByID(anchor.id)
            if (existing(anchorItem)) {
                const focusOffset = ensureFocusOffset(this.props.focusOffset, height)
                const expectedScroll =
                    anchorItem.offset - (focusOffset - (anchor.aboveFocus * anchorItem.height))
                this.adjustScroll = expectedScroll - containerScroll
            }

            segments.forEach((element, id) => {
                tweenHere(
                    element,
                    current => ensure(anchor.outlines.get(id), {...current, opacity: 0})
                ).catch(() => {})
            })
        }

        if (prevProps !== props) {
            publishAll<Props>(this.props, this.props$.publish)
        }
    }

    componentWillUnmount() {
        this.work.unsubscribe()
    }

    constructor(props: Props) {
        super(props)

        const height = 1000// TODO
        const rows = ensure(props.columns, 1)
        const layout = MasonryLayoutImpl.create(props.itemIDs, Math.round(height / 6), rows)
        const scroll = calcFocusScroll(props.focus, props.focusOffset, layout, height)
        const segments = calcSegments(scroll, scroll + height, layout)

        this.state = {layout, segments, useAutoLayout: true}
    }

    private readonly work: Subscription = new Subscription()

    private readonly children: Children = {
        container: document.createElement('div'),
        content: document.createElement('div'),
        viewport: document.createElement('div'),
        segments: new Map<ListItemID, HTMLElement>()
    }

    private readonly props$ = createDispatcher<Props>({
        itemIDs: distinctUntilChanged(),
        Item: distinctUntilChanged(),
        columns: distinctUntilChanged(),
        focus: distinctUntilChanged(),
        focusOffset: distinctUntilChanged(),
        onScroll: distinctUntilChanged(),
        onReflow: distinctUntilChanged()
    })

    private readonly actions$ = createDispatcher<Actions>({
        scrolled$: Pass,
        heightUpdated$: it => it.pipe(
            distinctUntilChanged(),
            // log$('height'),
            shareReplay(1)
        ),
        pinnedItems$: it => it.pipe(
            distinctUntilChanged(deepEqual),
            startWith([])
        ),
        touchStarted$: Pass,
        touchMoved$: Pass,
        touchEnded$: Pass,
        itemResized$: it => it.pipe(
            // log$('heightUpdated')
            filter(({id, height}) => this.state.segments.some(item => item.id === id))
        )
    })

    private get adjustScroll(): px {
        const {adjustScroll$} = this
        return (adjustScroll$ as BehaviorSubject<px>).getValue()
    }

    private set adjustScroll(scroll: px) {
        const {adjustScroll$} = this
        const {viewport} = this.children;

        viewport.style.transition = `none`
        viewport.style.transform =
            scroll === 0
                ? 'none'
                : `translateY(${-scroll}px)`;
        (adjustScroll$ as BehaviorSubject<px>).next(scroll)
    }

    private get containerScroll(): px {
        const {container} = this.children
        return container.scrollTop
    }

    private set containerScroll(scroll: px) {
        const {container} = this.children
        container.style['-webkit-overflow-scrolling'] = 'auto'
        container.scrollTop = scroll
        container.style['-webkit-overflow-scrolling'] = 'touch'
    }

    private get visualScroll(): px {
        const {viewport} = this.children
        return -calcOutline(viewport).y
    }

    private get height(): px {
        const {container} = this.children
        return container.offsetHeight
    }

    private velocity: Velocity = 0

    @Memoize()
    private get height$(): Observable<px> {
        const {heightUpdated$} = this.actions$

        return heightUpdated$.pipe(
            startWith(this.height)
        )
    }

    @Memoize()
    private get containerScroll$(): Observable<px> {
        const {scrolled$, touchStarted$, touchEnded$} = this.actions$

        const shouldSample: Observable<Pulse> = scrolled$.pipe(
            merge(touchStarted$),
            merge(touchEnded$),
            merge(interval(0, animationFrame)),
            timestamp(),
            distinctUntilChanged((prev, curr) => curr.timestamp - prev.timestamp < 100),
            sampleTime(0, animationFrame),
            startWith(Pulse)
        )

        return shouldSample.pipe(
            map(() => this.containerScroll),
            startWith(this.containerScroll),
            map(Math.round),
            distinctUntilChanged(),
            log$('containerScroll'),
            shareReplay(1)
        )
    }

    private readonly adjustScroll$: Observable<px> = new BehaviorSubject(0)

    @Memoize()
    private get velocity$(): Observable<Velocity> {
        const {containerScroll$} = this

        const velocity$ = containerScroll$.pipe(
            timestamp(),
            distinctUntilChanged((prev, curr) => curr.timestamp - prev.timestamp < 100),
            pairwise(),
            map(([prev, curr]) =>
                (curr.value - prev.value) / (curr.timestamp - prev.timestamp)
            ),
            bufferCount(3, 1),
            map(leastDeviation),
            filter(existing),
            switchMap(velocity =>
                interval(300).pipe(
                    mapTo(0),
                    startWith(velocity)
                )
            )
        )

        return velocity$.pipe(
            startWith(0),
            // log$('velocity'),
            tap(velocity => this.velocity = velocity),
            shareReplay(1)
        )
    }

    @Memoize()
    private get useAutoLayout$(): Observable<boolean> {
        const {velocity$} = this

        return velocity$.pipe(
            map(Math.abs),
            map(speed => speed > 3),
            bufferCount(2, 1),
            map(buffer => buffer.filter(isTrue).length > buffer.filter(isFalse).length),
            startWith(true),
            distinctUntilChanged(),
            // log$('useAutoLayout'),
            shareReplay(1)
        )
    }

    @Memoize()
    private get renderRange$(): Observable<{ from: px, to: px }> {
        const {velocity$, containerScroll$, adjustScroll$, height$} = this

        const renderRange$: Observable<{ from: px, to: px }> = containerScroll$.pipe(
            combineLatest(adjustScroll$),
            map(([container, viewport]) => container + viewport),
            combineLatest(height$, velocity$),
            map(([scroll, height, velocity]): { from: px, to: px } => ({
                from: scroll + Math.min(velocity * 500, -height),
                to: scroll + height + Math.max(velocity * 500, height)
            }))
        )

        return renderRange$.pipe(
            startWith({from: 0, to: this.height}),
            distinctUntilChanged(deepEqual),
            log$('renderRange'),
            shareReplay(1)
        )
    }

    @Memoize()
    private get layout$(): Observable<MasonryLayout> {
        const {itemIDs, columns} = this.props$
        const {itemResized$} = this.actions$
        const {renderRange$} = this

        const updates$: Observable<Array<ListItemShape>> = itemResized$.pipe(
            bufferTime(0, animationFrame),
            filter(buffer => buffer.length > 0),
            startWith([] as Array<ListItemShape>),
            distinctUntilChanged(deepEqual)
            // log$('updates$ in layout$')
        )

        const columns$: Observable<number> = columns.pipe(
            distinctUntilChanged(),
            filter(existing),
            startWith(ensure(this.props.columns, 1))
            // log$('columns$ in layout$')
        )

        const renderTo$: Observable<px> = renderRange$.pipe(
            map(range => range.to)
        )

        const layout$: Observable<MasonryLayout> = itemIDs.pipe(
            combineLatest(renderTo$, columns$, updates$),
            scan<[Array<ListItemID>, px, number, Array<ListItemShape>],
                [Array<ListItemID>, MasonryLayout]>(
                ([prevIDs, layout], [currIDs, renderTo, columns, updates]) => {
                    layout = currIDs === prevIDs ? layout : layout.derive(currIDs)
                    return [
                        currIDs,
                        layout.reflow(renderTo, columns).update(updates)
                    ]
                },
                [this.props.itemIDs, this.state.layout]
            ),
            map(([_, layout]) => layout)
        )

        return layout$.pipe(
            startWith(this.state.layout),
            distinctUntilChanged(),
            // log$('layout'),
            shareReplay(1)
        )
    }

    @Memoize()
    private get state$(): Observable<State> {
        const {layout$, renderRange$, useAutoLayout$} = this
        const {pinnedItems$} = this.actions$

        const state$: Observable<State> = renderRange$.pipe(
            combineLatest(useAutoLayout$, layout$, pinnedItems$),
            map(([viewport, useAutoLayout, layout, pinItems]): State => {
                const newView = calcSegments(viewport.from, viewport.to, layout)
                const pinnedItems = pinItems
                    .filter(id => !newView.some(view => view.id === id))
                    .map(id => layout.getByID(id))
                    .filter(existing)
                const segments = newView
                    .concat(pinnedItems)
                    .sort((a, b) => a.index - b.index)
                return {layout, segments, useAutoLayout}
            }),
            distinctUntilChanged<State>((prev, curr) =>
                prev.layout === curr.layout &&
                deepEqual(prev.segments, curr.segments) &&
                prev.useAutoLayout === curr.useAutoLayout
            )
        )

        return state$.pipe(
            startWith(this.state),
            // log$('state'),
            shareReplay(1)
        )
    }

    private setInitialScroll(): void {
        const {focus, focusOffset} = this.props
        const {container} = this.children
        const {layout} = this.state
        const height = container.offsetHeight

        container.scrollTop = calcFocusScroll(focus, focusOffset, layout, height)
    }

    private updateState(): Subscription {
        const {state$} = this
        return state$.subscribe(state => this.setState(state))
    }

    private callEventHandlers(): Subscription {
        const {containerScroll$, layout$} = this

        const work = new Subscription()

        work.add(containerScroll$.subscribe(scroll => {
            if (existing(this.props.onScroll))
                this.props.onScroll(scroll)
        }))
        work.add(layout$.subscribe(layout => {
            if (existing(this.props.onReflow))
                this.props.onReflow(layout)
        }))

        return work
    }

    private performContentJump(): Subscription {
        const {focus} = this.props$
        const {height$} = this
        const {touchStarted$} = this.actions$

        const focus$ = focus.pipe(
            distinctUntilChanged(),
            filter(existing)
        )

        const tweenJump = (focus: ListItemID, height: px): Observable<Pulse> =>
            Observable.create((observer: Observer<Pulse>) => {
                const {content} = this.children
                const {layout} = this.state
                const {containerScroll, adjustScroll, visualScroll} = this
                const {focusOffset} = this.props
                const {pinnedItems$} = this.actions$.publish

                const beforeY = -containerScroll
                const newScroll = calcFocusScroll(focus, focusOffset, layout, height)

                this.containerScroll = newScroll - adjustScroll
                const snapshot = calcSegments(
                    visualScroll - height,
                    visualScroll + height * 2,
                    layout
                ).map(it => it.id)
                pinnedItems$(snapshot)

                tweenHere(
                    content,
                    (snapshot, to) => ({...snapshot, y: beforeY}),
                    (from, to) => Math.min(Math.max(Math.abs(from.y - to.y), 200), 2000),
                    [0.445, 0.05, 0.55, 0.95]
                ).then(() => observer.complete()).catch(() => {})

                return () => {
                    this.containerScroll = this.visualScroll
                    this.adjustScroll = 0
                    terminateTween(content)
                    pinnedItems$([])
                }
            }).pipe(
                takeUntil(touchStarted$.pipe(skip(1)))
            )

        const jump$ = focus$.pipe(
            withLatestFrom(height$),
            switchMap(([focus, height]) => tweenJump(focus, height))
            // log$('jump')
        )

        return jump$.subscribe()
    }

    private applyAdjustedScroll(): Subscription {
        const {containerScroll$, layout$, height$} = this

        const isResizing$: Observable<boolean> = layout$.pipe(
            bufferTime(300),
            map(buffer => buffer.length !== 0),
            startWith(false)
            // log$('isScrolling')
        )

        const isScrolling$: Observable<boolean> = containerScroll$.pipe(
            bufferTime(300),
            map(buffer => buffer.length !== 0),
            startWith(false)
            // log$('isScrolling'),
        )

        const reachedEnd$: Observable<boolean> = containerScroll$.pipe(
            combineLatest(height$, layout$),
            map(([scroll, height, layout]) => scroll <= 0 || scroll - height >= layout.totalHeight),
            startWith(true)
            // log$('reachedEnd'),
        )

        const shouldApply$: Observable<Pulse> = isScrolling$.pipe(
            combineLatest(isResizing$, reachedEnd$),
            filter(([isScrolling, isResizing, reachedEnd]) => (!isScrolling && !isResizing) || reachedEnd)
            // log$('shouldApply', ()=> true)
        )

        return shouldApply$.subscribe(() => {
            const {adjustScroll, containerScroll} = this
            if (adjustScroll === 0)
                return
            this.containerScroll = containerScroll + adjustScroll
            this.adjustScroll = 0
        })
    }

    private Segment: React.ComponentType<ListItemPosition & State> =
        ((parent: this) => {
            const Measure = measuring('div')
            return class extends React.PureComponent<ListItemPosition & State> {
                render() {
                    const {Item} = parent.props
                    const {segments} = parent.children
                    const {onHeightChange} = this
                    const {index, offset, id, useAutoLayout} = this.props

                    const style: CSSProperties = {
                        position: useAutoLayout ? 'relative' : 'absolute',
                        display: 'block',
                        willChange: 'transform, opacity, top',
                        top: useAutoLayout ? 0 : offset,
                        contain: 'content',
                        width: '100%',
                        zIndex: index,
                        opacity: 1
                    }

                    return <Measure style={style}
                                    onHeightChange={onHeightChange}
                                    innerRef={ref => existing(ref)
                                        ? segments.set(id, ref)
                                        : segments.delete(id)
                                    }>
                        <Item id={id}/>
                    </Measure>
                }

                private onHeightChange = (height: px) => {
                    const {id} = this.props
                    parent.actions$.publish.itemResized$({id, height})
                }
            }
        })(this)
}

const Root = measuring('div')

type Props = {
    itemIDs: Array<ListItemID>
    Item: React.ComponentType<ItemProps>
    columns?: Maybe<number>
    focus?: Maybe<ListItemID>
    focusOffset?: px
    onScroll?: EventHandler<px>
    onReflow?: EventHandler<MasonryLayout>
}

type State = {
    layout: MasonryLayout
    segments: Array<MasonryItemPosition>
    useAutoLayout: boolean
}
type Children = {
    container: HTMLElement
    content: HTMLElement
    viewport: HTMLElement
    segments: Map<ListItemID, HTMLElement>
}

type Actions = {
    scrolled$: React.SyntheticEvent<HTMLDivElement>
    heightUpdated$: px
    pinnedItems$: ReadonlyArray<ListItemID>
    touchStarted$: React.TouchEvent<HTMLDivElement>
    touchMoved$: React.TouchEvent<HTMLDivElement>
    touchEnded$: React.TouchEvent<HTMLDivElement>
    itemResized$: ListItemShape
}

type Snapshot = {
    anchor: Maybe<Anchor>
}

type Anchor = {
    id: ListItemID
    aboveFocus: percent
    outlines: Map<ListItemID, Outline>
}
