import * as loremIpsum from 'lorem-ipsum'
import * as React from 'react';
import {ensure} from '../../../utils/ensure'
import {getRandomIntInclusive} from '../../../utils/getRandomIntInclusive'
import {ItemProps} from '../src/ItemProps';

export class SegmentExample extends React.Component<ItemProps, State> {
    componentDidMount() {
        this.interval = setInterval(() => {
            if (this.state.type !== 'text')
                return
            const text = loremIpsum({count: getRandomIntInclusive(3, 10)})
            textCache[this.props.id] = text
            this.setState({type: 'text', text})
        }, getRandomIntInclusive(3000, 5000))
    }

    componentWillUnmount() {
        clearInterval(this.interval)
    }

    render() {
        return <div style={{
            position: 'relative',
            display: 'block',
            width: "100%",
            minHeight: 50,
            backgroundColor: 'white',
            wordWrap: 'break-word',
            wordBreak: 'break-all'
            // userSelect: 'none' //FIXME triggers bug(animation stops). why?
        }}>
            <p>{this.props.id}</p>
            {
                this.state.type === 'text'
                    ? this.state.text
                    : <img style={{minWidth: '100%', height: 'auto', display: 'block'}}
                           src={this.state.image}/>
            }
        </div>
    }

    constructor(props: ItemProps) {
        super(props)
        const type = ensure(typeCache[props.id], Math.random() > 0.2 ? 'text' : 'image')
        typeCache[props.id] = type

        // if (type === 'text') {
        //     let text = textCache[props.id] || loremIpsum({count: getRandomIntInclusive(3, 10)})
        //     textCache[props.id] = text
        //     this.state = {type: 'text', text}
        // } else {
            const image = imgCache[props.id]
                || `https://picsum.photos/500/${getRandomIntInclusive(300, 800)}`
            imgCache[props.id] = image
            this.state = {type: 'image', image}
        // }
    }

    private interval: any
}

const typeCache: { [id: string]: 'text' | 'image' } = {}
const textCache: { [id: string]: string } = {}
const imgCache: { [id: string]: string } = {}

type State = { type: 'text', text: string }
    | { type: 'image', image: string }


