import FastBinaryIndexedTree from 'fast-binary-indexed-tree'
import {is, List, Map} from 'immutable'
import {existing, notExisting} from '../typecheck/existing'
import {isNonpositiveInteger} from '../typecheck/isInteger'
import {assume} from '../utils/assume'
import {cloneFastBinaryIndexedTree} from '../utils/cloneFastBinaryIndexedTree'
import {ensure} from '../utils/ensure'
import {nothing} from '../utils/nothing'
import {byIndex} from './byIndex'
import {ListItemID, ListItemPosition, ListItemShape, ListLayout} from './ListLayout'

export class ListLayoutImpl implements ListLayout {
    public get itemIDs(): ReadonlyArray<ListItemID> { return this.indexToID.toArray() }

    public get size(): number { return this.indexToID.size }

    public get totalHeight(): px {
        return ensure(this.prefixSumIndex.read(this.indexToID.size), 0)
    }

    public get updatedHeights(): ReadonlyMap<ListItemID, px> { return this.knownHeights }

    public getByID(id: ListItemID): Maybe<ListItemPosition> {
        const {idToIndex, prefixSumIndex} = this
        const index = idToIndex.get(id)
        if (notExisting(index))
            return nothing
        const offset = prefixSumIndex.read(index)
        const height = prefixSumIndex.readSingle(index)
        return {id, index, offset, height}
    }

    public getByIndex(index: number): Maybe<ListItemPosition> {
        const {indexToID} = this
        if (!isNonpositiveInteger(index))
            throw new Error(`${index} is not a legal index`)

        const id = indexToID.get(index)
        if (notExisting(id))
            return nothing
        return this.getByID(id)
    }

    public getByIndexRange(from: number, to: number): Array<ListItemPosition> {
        const {size} = this
        from = Math.max(from, 0)
        to = Math.min(to, size - 1)

        const results: Array<ListItemPosition> = []
        for (let i = from; i <= to; i++) {
            const item = this.getByIndex(i)
            if (notExisting(item))
                break
            results.push(item)
        }
        return results.sort(byIndex)
    }

    public getByOffset(position: px): Maybe<ListItemPosition> {
        const {prefixSumIndex, size} = this
        let bound = prefixSumIndex.upperBound(position)
        const index = bound === size ? bound - 1 : bound
        const id = this.indexToID.get(index)
        if (notExisting(id))
            return nothing
        return this.getByID(id)
    }

    public getByOffsetRange(from: px, to: px): Array<ListItemPosition> {
        const {totalHeight} = this
        from = Math.max(from, 0)
        to = Math.min(to, totalHeight - 1)

        const fromItem = this.getByOffset(Math.max(from, 0))
        const toItem = this.getByOffset(Math.min(to, this.totalHeight))
        if (notExisting(fromItem) || notExisting(toItem)) {
            return []
        }
        return this.getByIndexRange(fromItem.index, toItem.index)
    }

    public derive(itemIDs: ReadonlyArray<ListItemID>): ListLayout {
        const {indexToID, defaultHeight, knownHeights} = this

        const newIndexToID = List(itemIDs)

        if (is(indexToID, newIndexToID)) {
            return this
        }

        const newIDToIndex = Map<ListItemID, number>().withMutations(newIDToIndex => {
            newIndexToID.forEach((id, index) =>
                newIDToIndex.set(id, index)
            )
        })

        const newPrefixSumIndex = new FastBinaryIndexedTree({
            defaultFrequency: defaultHeight,
            maxVal: maxSize
        })
        knownHeights.forEach((height, id) => {
            const index = newIDToIndex.get(id)
            if (existing(index)) {
                newPrefixSumIndex.writeSingle(index, height)
            }
        })

        return new ListLayoutImpl(
            defaultHeight,
            newIndexToID,
            newIDToIndex,
            knownHeights,
            newPrefixSumIndex
        )
    }

    public append(additionalItemIDs: ReadonlyArray<ListItemID>): ListLayout {
        const {defaultHeight, knownHeights, prefixSumIndex, idToIndex, indexToID} = this

        if (additionalItemIDs.length === 0)
            return this

        if (additionalItemIDs.some(id => idToIndex.has(id)))
            throw new Error('adding existing id')

        const newPrefixSumIndex = cloneFastBinaryIndexedTree(prefixSumIndex)
        const newIndexToID = indexToID.concat(additionalItemIDs)
        const newIDToIndex = idToIndex.withMutations(idToIndex => {
            additionalItemIDs.forEach((id, indexDiff) => {
                const index = indexDiff + indexToID.size
                idToIndex.set(id, index)
                assume(knownHeights.get(id), it => newPrefixSumIndex.writeSingle(index, it))
            })
        })

        return new ListLayoutImpl(
            defaultHeight,
            newIndexToID,
            newIDToIndex,
            knownHeights,
            newPrefixSumIndex
        )
    }

    public update(newShapes: ReadonlyArray<ListItemShape>): ListLayout {
        const {defaultHeight, indexToID, idToIndex, knownHeights, prefixSumIndex} = this

        if (newShapes.length === 0 ||
            newShapes.every(({id, height}) =>
                notExisting(idToIndex.get(id)) || knownHeights.get(id) === height)) {
            return this
        }

        const newPrefixSumIndex = cloneFastBinaryIndexedTree(prefixSumIndex)
        const newKnownHeights = knownHeights.withMutations(knownHeights => {
            newShapes.forEach(({id, height}) => {
                knownHeights.set(id, height)
                const index = idToIndex.get(id)
                assume(index, index => newPrefixSumIndex.writeSingle(index, height))
            })
        })

        return new ListLayoutImpl(
            defaultHeight,
            indexToID,
            idToIndex,
            newKnownHeights,
            newPrefixSumIndex
        )
    }

    public clone(): ListLayout {
        const {indexToID, idToIndex, knownHeights, defaultHeight, prefixSumIndex} = this

        const newPrefixSumIndex = cloneFastBinaryIndexedTree(prefixSumIndex)

        return new ListLayoutImpl(
            defaultHeight,
            indexToID,
            idToIndex,
            knownHeights,
            newPrefixSumIndex
        )
    }

    public static create(itemIDs: ReadonlyArray<ListItemID>,
                         defaultHeight: px,
                         knownHeights?: Map<ListItemID, px>): ListLayout {

        const indexToID: List<ListItemID> = List(itemIDs)
        const idToIndex: Map<ListItemID, number> =
            Map<ListItemID, number>().withMutations(idToIndex =>
                indexToID.forEach((id, index) =>
                    idToIndex.set(id, index)
                )
            )
        knownHeights = ensure(knownHeights, Map<ListItemID, px>())

        const prefixSumIndex = new FastBinaryIndexedTree({
            defaultFrequency: defaultHeight,
            maxVal: maxSize
        })

        return new ListLayoutImpl(
            defaultHeight,
            indexToID,
            idToIndex,
            knownHeights,
            prefixSumIndex
        )
    }

    // copy-on-write style data sharing
    private constructor(
        private readonly defaultHeight: px,
        private readonly indexToID: List<ListItemID>,
        private readonly idToIndex: Map<ListItemID, number>,
        private readonly knownHeights: Map<ListItemID, px>,
        private readonly prefixSumIndex: ReadonlyFastBinaryIndexedTree
    ) {}
}

export class DuplicateListItemIDError extends Error {}

const maxSize = 100000 // TODO
