import * as React from 'react'
import * as ReactDOM from 'react-dom';
import ResizeObserver from 'resize-observer-polyfill'
import {existing, notExisting} from '../../typecheck/existing'
import {isString} from '../../typecheck/isString'
import {nothing} from '../../utils/nothing'
import {InnerRef} from '../InnerRef'
import {PropsOf} from '../PropsOf';
import {Referable} from '../Referable'
import {RefOf} from '../RefOf'
import hoistNonReactStatics = require('hoist-non-react-statics')

export function measuring<C extends Referable>(
    Component: C
): Measuring<C> {
    class HOC extends React.PureComponent<PropsOf<C> & MeasureProps<C>> {
        render() {
            // TODO what if Component renders multiple elements?

            const props: PropsOf<C> = Object.assign(
                {},
                this.props,
                {ref: (it: any) => this.updateRef(it)}
            ) as any
            delete props['onHeightChange']
            delete props['onWidthChange']
            delete props['innerRef']

            return React.createElement(
                Component as any,
                props,
                this.props.children
            )
        }

        private observer: ResizeObserver
        private observing: Maybe<Element>

        constructor(props: PropsOf<C> & MeasureProps<C>) {
            super(props)

            this.observer = new ResizeObserver(
                (entries: ResizeObserverEntry[], observer: ResizeObserver) => {
                    const height = entries[0].contentRect.height
                    const width = entries[0].contentRect.width
                    if (existing(this.props.onHeightChange)) {
                        this.props.onHeightChange(height)
                    }
                    if (existing(this.props.onWidthChange)) {
                        this.props.onWidthChange(width)
                    }
                }
            )
        }

        componentWillUnmount() {
            this.observer.disconnect()
        }

        private updateRef(ref: Maybe<RefOf<C>>) {
            if (existing(this.observing)) {
                this.observer.unobserve(this.observing)
                this.observing = nothing
            }
            if (existing(this.props.innerRef)) {
                this.props.innerRef(ref)
            }
            if (notExisting(ref)) {
                return
            }
            const dom: Element = ref instanceof Element
                ? ref
                : ReactDOM.findDOMNode(ref)

            if (existing(dom)) {
                this.observer.observe(dom)
                this.observing = dom
            }
        }
    }

    if (!isString(Component)) {
        hoistNonReactStatics(HOC, Component as any)
    }
    if (isString(Component)) {
        HOC['displayName'] = `Measuring<${Component}>`
    }

    return HOC
}

export type Measuring<A extends Referable> = React.ComponentClass<PropsOf<A> & MeasureProps<A>>

type MeasureProps<T extends Referable> = {
    onHeightChange?: EventHandler<px>
    onWidthChange?: EventHandler<px>
} & InnerRef<T>
