import {applyToPoints} from 'transformation-matrix/applyToPoint'

export function applyTransform(before: Outline, transform: Matrix): Outline {
    const {x, y, width, height} = before
    const afterP = applyToPoints(transform, [{x, y}, {x: x + width, y: y + height}])

    const [afterX, afterX2] = afterP.map(p => p.x)
    const [afterY, afterY2] = afterP.map(p => p.y)
    const [afterWidth, afterHeight] = [afterX2 - afterX, afterY2 - afterY]
    const after: Outline = {
        x: afterX,
        y: afterY,
        width: afterWidth,
        height: afterHeight,
        opacity: before.opacity
    }
    return after
}
