import {inverse} from 'transformation-matrix/inverse'
import {toCSS} from 'transformation-matrix/toString'
import {isFunction} from '../typecheck/isFunction'
import {forDuration} from '../utils/forDuration'
import {nextAnimationFrame} from '../utils/nextAnimationFrame'
import {uniqueID} from '../utils/uniqueID'
import {calcOutline} from './calcOutline'
import {calcOutlineOrigin} from './calcOutlineOrigin'
import {intermediateTransform} from './intermediateTransform'

export async function tweenHere(
    element: HTMLElement,
    from: Outline | ((current: Outline, to: Outline) => Outline) = current => current,
    duration: ms | ((from: Outline, target: Outline) => ms) = 200,
    easing: CubicBezierParam = [0, 0, 1, 1]
): Promise<void> {
    const tweeningID = uniqueID()
    tweening.set(element, tweeningID)

    const current = calcOutline(element)
    const to = calcOutlineOrigin(element)

    from = isFunction(from) ? from(current, to) : from

    const transform = intermediateTransform(from, to)
    duration = isFunction(duration) ? duration(from, to) : duration
    element.style.transition = 'none'
    element.style.transform = toCSS(inverse(transform))
    element.style.opacity = `${from.opacity}`

    await nextAnimationFrame()
    await nextAnimationFrame()
    if (tweening.get(element) !== tweeningID) {
        throw new TweenInterrupted(element, from, to)
    }
    element.style.transition = `
        opacity ${duration}ms cubic-bezier(${easing.join(',')}),
        transform ${duration}ms cubic-bezier(${easing.join(',')})
    `
    element.style.transform = `none`
    element.style.opacity = `${to.opacity}`

    await forDuration(duration)
    if (tweening.get(element) !== tweeningID) {
        throw new TweenInterrupted(element, from, to)
    }
    element.style.transition = `none`
    tweening.delete(element)
}

export function terminateTween(element: HTMLElement): void {
    tweening.delete(element)
    element.style.transition = 'none'
    element.style.transform = 'none'
}

const tweening: WeakMap<Element, TweenID> = new WeakMap()

type TweenID = string

export class TweenInterrupted extends Error {
    constructor(public readonly element: HTMLElement,
                public readonly from: Outline,
                public readonly to: Outline) {
        super('Tween interrupted.')
    }
}

