// ignoring rotate

import {hasStyle} from './hasStyle'

export function calcOutline(dom: Element): Outline {
    const rect = dom.getBoundingClientRect()

    const {width, height} = rect
    const x = rect.left
    const y = rect.top

    const opacityCSS = getComputedStyle(dom).opacity
    const opacity = hasStyle(opacityCSS) ? parseFloat(opacityCSS) : 0

    return {x, y, width, height, opacity}
}
