import {notExisting} from '../typecheck/existing'
import {nothing} from './nothing'

export function calcSum(array: Array<number>): number {
    return array.reduce((acc, curr) => acc + curr, 0)
}

export function calcAverage(array: Array<number>): Maybe<number> {
    if (array.length === 0)
        return nothing
    return calcSum(array) / array.length
}

export function leastDeviation(array: Array<number>): Maybe<number> { // TODO better name
    const average = calcAverage(array)
    if (notExisting(average))
        return nothing
    const sorted = array.sort((a, b) => Math.abs(a - average) - Math.abs(b - average))
    return sorted[0]
}

export function calcDeviation(array: Array<number>): Maybe<number> {
    const average = calcAverage(array)
    if (array.length === 0 || notExisting(average))
        return nothing
    return calcAverage(array.map(num => Math.abs(num - average)))
}


