import {notExisting} from '../typecheck/existing'
import {isFunction} from '../typecheck/isFunction'
import {nothing} from './nothing'

export function assume<A, B>(a: A, func: (a: NonNullable<A>) => B): Maybe<B>
export function assume<A, B, C>(a: A, b: Maybe<B>, func: (a: A, b: B) => C): Maybe<C>
export function assume<T>(
    ...params: Array<any | ((...p: Array<NonNullable<any>>) => T)>
): Maybe<T> {
    const func = params[params.length - 1]
    const funcParams = params.slice(0, params.length - 1)
    if (!isFunction(func) || funcParams.length < 1 || funcParams.some(notExisting)) {
        return nothing
    }
    return func(...funcParams)
}
